
      
import { LitElement, html } from 'lit-element';
import { transition, slide } from 'lit-transition';

export class Comp extends LitElement {
  static get properties() {
    return { 
      a: Boolean, // to toggle content
      mode: String // for transition mode
    }
  }

  // sets mode and swaps transitioned content
  exec(mode) {
    this.mode = mode;
    this.a = !this.a;
  }

  render() {
    // animates with different modes
    return html`click to transition
    <button @click=${() => this.exec('in-out')}>in-out</button>
    <button @click=${() => this.exec('out-in')}>out-in</button>
    <button @click=${() => this.exec('both')}>both</button>
    ${transition(this.a
      ? html`<div>A ${this.mode}</div>`
      : html`<div>B ${this.mode}</div>`,
      slide({x:'200px', mode: this.mode})
    )}`;
  } 
}

      export const code = "\nimport { LitElement, html } from 'lit-element';\nimport { transition, slide } from 'lit-transition';\n\nexport class Comp extends LitElement {\n  static get properties() {\n    return { \n      a: Boolean, // to toggle content\n      mode: String // for transition mode\n    }\n  }\n\n  // sets mode and swaps transitioned content\n  exec(mode) {\n    this.mode = mode;\n    this.a = !this.a;\n  }\n\n  render() {\n    // animates with different modes\n    return html`click to transition\n    <button @click=${() => this.exec('in-out')}>in-out</button>\n    <button @click=${() => this.exec('out-in')}>out-in</button>\n    <button @click=${() => this.exec('both')}>both</button>\n    ${transition(this.a\n      ? html`<div>A ${this.mode}</div>`\n      : html`<div>B ${this.mode}</div>`,\n      slide({x:'200px', mode: this.mode})\n    )}`;\n  } \n}\n";
    